import { Settings as EyesSettings } from 'eyes';

/**
 * AsteroidsSettings.
 */
export class AsteroidsSettings extends EyesSettings {
	/**
	 * Toggle info panel override.
	 */
	toggleInfoPanel() {
		const tutorialIndex = 0;
		const goHome = true;
		this._app.getComponent('tutorialOverlay')?.navigateToTutorial(tutorialIndex, goHome);
	}
}
