/**
 * Cancel token class.
 */
export class CancelToken {
	/**
	 * Constructor.
	 */
	constructor() {
		this._isCanceled = false;
	}

	/**
	 * Sets the state to canceled.
	 */
	cancel() {
		this._isCanceled = true;
	}

	/**
	 * Gets canceled state.
	 * @returns {boolean}
	 */
	get isCanceled() {
		return this._isCanceled;
	}
}
