import {
	BaseApp,
	BaseComponent
} from '../../internal';
import html from './toast.html';
import './toast.css';

/**
 * Toast component.
 *
 * A toast is essentially an alert that appears on the screen for a short period of time.
 * Currently we can only display one toast at a time.
 * @extends BaseComponent
 */
export class Toast extends BaseComponent {
	/**
	 * Constructor.
	 * @param {BaseApp} app
	 * @param {object} [options={}]
	 */
	constructor(app, options = {}) {
		// Add state options
		super(app, null, {
			isVisible: false,
			iconClass: 'icon-locked',
			toastContent: 'TOAST!',
			hideDelay: 2000,
			...options
		});

		/**
		 * The hideTimeout is the timeout that will hide the toast.
		 * @type {number|null}
		 * @private
		 */
		this._hideTimeout = null;
	}

	/**
	 * Sets the icon class
	 * @param {string} iconClass
	 */
	setIcon(iconClass) {
		this.setState({
			iconClass
		});
	}

	/**
	 * Set the toast content
	 * @param {string} content
	 */
	setContent(content) {
		this.setState({
			toastContent: content
		});
	}

	/** @override */
	show() {
		super.show();

		// Set the hide timeout if we have a hide delay.
		const { hideDelay } = this._state;
		if (hideDelay) {
			clearTimeout(this._hideTimeout);

			this._hideTimeout = setTimeout(() => {
				this.hide();
			}, hideDelay);
		}
	}

	/** @override */
	hide() {
		super.hide();
		// Clear the hide timeout.
		clearTimeout(this._hideTimeout);
	}
}

Toast.html = html;
