import {
	BaseApp,
	StoryBaseContentBlock
} from '../../../../internal';
import html from './toggle_block.html';
import './toggle_block.css';

/**
 * Block for description.
 */
export class ToggleBlock extends StoryBaseContentBlock {
	/**
	 * Constructor.
	 * @param {BaseApp} app
	 * @param {object} [options={}]
	 */
	constructor(app, options = {}) {
		super(app, {
			isSelected: false,
			...options
		});

		this._class.isSelectedClass = {
			true: 'selected',
			false: ''
		};

		// Make sure this is a boolean value
		this._config.info.selected = Boolean(this._config.info.selected);

		Object.assign(this._state, {
			isSelectedClass: this._class.isSelectedClass[this._state.isSelected]
		});
	}

	/**
	 * Called on entering the slide containing block.
	 * @returns {Promise}
	 */
	async onEnter() {
		await super.onEnter();
		if (this._config.info.selected !== this._state.isSelected) {
			await this.onClick();
		}
	}

	/**
	 * Called on leaving the slide containing block.
	 * @returns {Promise}
	 */
	async onLeave() {
		await super.onLeave();
		if (this._state.isSelected) {
			await this.onClick();
		}
	}

	/**
	 * Called on clicking block.
	 * @returns {Promise}
	 */
	async onClick() {
		await super.onClick();
		if (this._state.isSelected) {
			this.unselect();
			console.log('unselect');
		}
		else {
			this.select();
			console.log('select');
		}
	}

	/**
	 * Select button.
	 */
	select() {
		this.setState({
			isSelected: true,
			isSelectedClass: this._class.isSelectedClass.true
		});
	}

	/**
	 * Unselect button.
	 */
	unselect() {
		this.setState({
			isSelected: false,
			isSelectedClass: this._class.isSelectedClass.false
		});
	}
}

ToggleBlock.html = html;
