import { AppUtils } from 'eyes';

// An incrementing int used to cancel outdated camera transition awaits
let globalNonce = 0;

export default {
	type: 'nonsequential',
	entities: [
		'4_vesta',
		'67p_churyumov_gerasimenko'
	],
	slides: [
		{
			id: 'slide_1',
			type: 'overlay',
			classList: ['opaque', 'black'],
			content: [
				{
					type: 'title',
					title: 'Asteroids and Comets 101'
				}
			]
		},
		{
			id: 'slide_2',
			type: 'overlay',
			classList: ['opaque', 'black'],
			content: [
				{
					type: 'image',
					src: 'assets/images/outterAsteroidBelt.png',
					title: 'Main Asteroid Belt. Credit: NASA/JPL',
					alt: 'This image illustrates the millions of asteroids between the Sun and Jupiter  know as the asteroid belt.',
					clickable: true
				},
				{
					type: 'description',
					description: 'Asteroids and comets are remnants left over from the early formation of our solar system 4.6 billion years ago. Asteroids are mostly rocky bodies that formed closer to the Sun than Jupiter, while comets formed farther from the Sun and contain substantial amounts of frozen ices. The vast majority of these small bodies are asteroids, and most of them reside within the main belt, between the orbits of Mars and Jupiter. <br><br>The current number of known asteroids in the entire solar system is:<br><span class="big semi tween-count test">0</span><br><br>',
					onEnter: async (app, block) => {
						const element = block.element.querySelector('.tween-count');
						const response = await fetch('https://solarsystem.nasa.gov/api/v1/static_stellar_counts/1/');
						const count = (await response.json()).asteroids;

						AppUtils.tween({ i: 0 }, { i: count }, {
							onUpdate: object => {
								if (element) {
									const value = Math.round(object.i);
									element.innerHTML = Number(value).toLocaleString();
								}
							},
							duration: 3000
						});
					}
				}
			]
		},
		{
			id: 'slide_3',
			type: 'panel',
			content: [
				{
					type: 'description',
					description: 'Asteroids range in size from Vesta (shown here)  one of the largest at about 329 miles (530 kilometers) in diameter  to bodies that are as small as 3 feet (1 meter) across. Most asteroids are small: the total mass of all them combined is far less than that of the Earths Moon.<br>Asteroids less than about one meter across are called meteoroids, and are generally too small to be detected by telescopes. Meteoroids (or asteroids) that happen to hit Earths atmosphere become visible as meteors and largely disintegrate. Larger asteroids (car-sized or bus-sized) produce bright meteors called fireballs or bolides, but they also mostly disintegrate. Any solid leftover pieces that make it all the way to the ground are called meteorites.'
				}
			],
			camera: [
				async app => {
					const localNonce = ++globalNonce;
					await app.getManager('scene').isReady('4_vesta');
					if (localNonce !== globalNonce) return;
					await app.cameraScripts.goToSystem('inner_solar_system', { duration: 1 });
					if (localNonce !== globalNonce) return;
					await app.cameraScripts.goToCelestialObject('4_vesta', { duration: 4 });
				}
			]
		},
		{
			id: 'slide_4',
			type: 'panel',
			content: [
				{
					type: 'description',
					description: 'Comets (like 67P/Churyumov-Gerasimenko, shown here) also have dust and rock, but also contain large amounts of various frozen ices. Comets generally follow long elliptical orbits in which they spend most of their time far from the Sun, where the ices can remain frozen.<br>When a comets orbit brings it closer to the Sun, the ices start heating up, creating explosive jets which spew out gas and dust. The expelled material forms into a cloud around the solid nucleus called the coma, that can be larger than a planet. The material is also swept back away from the Sun into diffuse tails that can stretch for millions of miles (use Shift-S to zoom out quickly to see the tail, Shift-W to zoom in; pinch to zoom on mobiles). Eventually, after thousands of orbits, a comets ices will be depleted. Some objects now classified as asteroids may simply be dead comets.'
				}
			],
			camera: [
				async app => {
					const localNonce = ++globalNonce;
					await app.getManager('scene').isReady('67p_churyumov_gerasimenko');
					if (localNonce !== globalNonce) return;
					await app.cameraScripts.goToSystem('inner_solar_system', { duration: 1 });
					if (localNonce !== globalNonce) return;
					await app.cameraScripts.goToCelestialObject('67p_churyumov_gerasimenko', { duration: 4 });
				}
			]
		},
		{
			id: 'slide_5',
			type: 'panel',
			content: [
				{
					type: 'description',
					description: 'Many asteroids orbit nearby. On occasion, one impacts our planet. For example, the dinosaurs are thought to have been made extinct by the impact of a large asteroid 6-9 miles wide (10-15 km) that collided with Earth 66 million years ago.<br>NASA established the <a class="clickable" href="https://www.nasa.gov/planetarydefense/overview" target="_blank">Planetary Defense Coordination Office (PDCO)</a> to manage its ongoing mission of planetary defense, which includes tracking potentially hazardous objects. The PDCO has a lead role in coordinating U.S. government planning for response to an actual impact threat, and is supported by the <a class="clickable" href="https://cneos.jpl.nasa.gov" target="_blank">Center for Near-Earth Object Studies (CNEOS)</a>, which is NASAs center for computing asteroid and comet orbits and their odds of impacting Earth. <br>For more on close approaches, click <a class="clickable" href="#/story/asteroids_close_approach">here</a>.'
				}
			],
			camera: [
				async app => {
					++globalNonce;
					await app.cameraScripts.goToSystem('inner_solar_system', { duration: 2 });
				}
			],
			rate: 604800
		}
	]
};
