import { SelectionManager as EyesSelectionManager } from 'eyes';

/**
 * @inheritdoc
 * @extends EyesSelectionManager
 */
export class SelectionManager extends EyesSelectionManager {
	/**
	 * @inheritdoc
	 * @override
	 */
	_getLink(entityName) {
		const link = super._getLink(entityName);
		return this._app.getManager('link')?.getParsedLink?.(link) || link;
	}
}
