import {
	BaseComponent
} from '../../internal';
import html from './load_icon.html';

/**
 * @inheritdoc
 * @extends BaseComponent
 */
class LoadIcon extends BaseComponent {
	/** @inheritdoc */
	init() {
		super.init();

		this._callbackRegistry.push(
			{
				emitter: this._app.getManager('scene'),
				event: 'loading',
				callback: this.show
			},
			{
				emitter: this._app.getManager('scene'),
				event: 'loaded',
				callback: this.hide
			}
		);

		this.hide();
	}
}

LoadIcon.html = html;

export default LoadIcon;
