			align = entity.addControllerByClass(Pioneer.AlignController);
			align.setJoint('20_P4_Truss_02');
			align.setPrimaryAxis(vectorForBetaGimbalAxis);
			align.setSecondaryAxis(vectorForBetaGimbalPoint);
