import { LabelManager as EyesLabelManager } from 'eyes';

/**
 * @inheritdoc
 * @extends EyesSelectionManager
 */
export class LabelManager extends EyesLabelManager {
	/**
	 * @inheritdoc
	 * @override
	 */
	_getLink(entityName) {
		const link = super._getLink(entityName);
		return this._app.getManager('link')?.getParsedLink?.(link) || link;
	}
}
