import { BaseApp, CarouselPanel } from 'eyes';

import './following_panel.css';

/**
 * FollowingPanel extends CarouselPanel
 */
export class FollowingPanel extends CarouselPanel {
	/**
	 * Constructor.
	 * @param {BaseApp} app
	 * @param {object} options
	 */
	constructor(app, options) {
		super(app, {
			// Class names
			panelTypeClass: 'following-panel',

			// Options
			title: '',
			preTitle: 'following',

			...options
		});
	}
}
