import {
	BaseApp,
	StoryBaseContentBlock
} from '../../../../internal';
import html from './description_block.html';
import './description_block.css';

/**
 * Block for description.
 */
export class DescriptionBlock extends StoryBaseContentBlock {
	/**
	 * Constructor.
	 * @param {BaseApp} app
	 * @param {object} [options={}]
	 */
	constructor(app, options = {}) {
		super(app, {
			hasMore: false,
			isMoreVisible: false,
			...options
		});

		this._class.hasMoreClass = {
			true: '',
			false: 'hidden'
		};

		this._more = {
			isVisible: {
				moreMessage: 'Show Less',
				moreIcon: 'icon-minus',
				moreClass: ''
			},
			isHidden: {
				moreMessage: 'Show More',
				moreIcon: 'icon-plus',
				moreClass: 'hidden'
			}
		};

		Object.assign(this._state, this._more.isHidden, {
			hasMoreClass: this._class.hasMoreClass[this._state.hasMore]
		});
	}

	/** @inheritdoc */
	init() {
		super.init();

		const hasMore = Boolean(this._config.info.more);
		this.setState({
			hasMore,
			hasMoreClass: this._class.hasMoreClass[hasMore]
		});
	}

	/**
	 * Show more block.
	 */
	showMore() {
		this.setState({
			isMoreVisible: true,
			...this._more.isVisible
		});
	}

	/**
	 * Hide more block.
	 */
	hideMore() {
		this.setState({
			isMoreVisible: false,
			...this._more.isHidden
		});
	}

	/**
	 * Show/hide more block.
	 */
	toggleMore() {
		if (this._state.isMoreVisible) {
			this.hideMore();
		}
		else {
			this.showMore();
		}
	}
}

DescriptionBlock.html = html;
