import {
	StoryBaseContentBlock
} from '../../../../internal';
import html from './replay_button_block.html';
import './replay_button_block.css';

/**
 * Block for button.
 */
export class ReplayButtonBlock extends StoryBaseContentBlock {
	/** @inheritdoc */
	init() {
		if (!this._config.info.text) {
			this._config.info.text = this._config.info.fromStart ? 'Restart Story' : 'Replay Animation';
		}
		super.init(this._config.info);
	}

	/**
	 * @inheritdoc
	 */
	async onClick() {
		const router = this._app.getManager('router');
		if (this._config.info.fromStart) {
			// Replay from start
			router.navigate(router.currentRoute.url);
		}
		else {
			// Replay current slide
			router.reload();
		}
	}
};

ReplayButtonBlock.html = html;
